html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.container {
  max-width: 1170px;
  margin: 0 auto;
}

@media only screen and (max-width: 1200px) {
  .container {
    margin: 0 20px;
  }
}

.btn > span {
  display: inline-block;
  transition: transform .5s, opacity .5s;
  transition-timing-function: linear;
  transform: translate3d(-10px, -10px, 0);
  opacity: 0;
  color: #000;
  text-shadow: black .1em .1em .2em;
}

.btn:before {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  content: attr(data-text);
  transition: transform .3s, opacity .3s;
  transition-timing-function: linear;
}

.btn:hover:before, .btn:focus:before {
  transform: translate3d(100%, 100%, 0);
  opacity: 0;
}

.btn:hover > span, .btn:focus > span {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  color: #000;
}

.btn:hover > span:nth-child(1), .btn:focus > span:nth-child(1) {
  transition-delay: .03s;
}

.btn:hover > span:nth-child(2), .btn:focus > span:nth-child(2) {
  transition-delay: .06s;
}

.btn:hover > span:nth-child(3), .btn:focus > span:nth-child(3) {
  transition-delay: .09s;
}

.btn:hover > span:nth-child(4), .btn:focus > span:nth-child(4) {
  transition-delay: .12s;
}

.btn:hover > span:nth-child(5), .btn:focus > span:nth-child(5) {
  transition-delay: .15s;
}

.btn:hover > span:nth-child(6), .btn:focus > span:nth-child(6) {
  transition-delay: .18s;
}

.btn:hover > span:nth-child(7), .btn:focus > span:nth-child(7) {
  transition-delay: .21s;
}

.btn:hover > span:nth-child(8), .btn:focus > span:nth-child(8) {
  transition-delay: .24s;
}

.btn:hover > span:nth-child(9), .btn:focus > span:nth-child(9) {
  transition-delay: .27s;
}

@font-face {
