.maske {
  position: fixed;
  z-index: 2000;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  background: #000;
  mix-blend-mode: darken;
  animation-name: slide;
  animation-duration: 3s;
  animation-fill-mode: forwards;
}

@keyframes slide {
  99% {
    height: 100vh;
  }
  100% {
    height: 0vh;
  }
}

.maske__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150vw;
  height: 150vh;
  transform: translate(-50%, -50%) scale(0.01);
  border-radius: 50%;
  background: #fff;
  animation-name: slidein;
  animation-duration: 3s;
  animation-fill-mode: forwards;
}

@keyframes slidein {
  0% {
    transform: translate(-50%, -50%) scale(0.01);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}

