.content:after {
  content: " ";
  display: table;
  clear: both;
}

.content__left {
  float: left;
  position: relative;
  left: -1000px;
  max-width: 50%;
  opacity: 0;
  animation-name: contentl;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  font-size: 0;
}

